@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  margin: 0;
  padding: 0;
}

body {
  background-color: #000;
  font-family: "Roboto", sans-serif;
  overflow-x: hidden;
}

.navbar-brand > img {
  border-radius: 50%;
}

.navbar {
  font-family: "Kaushan Script", cursive;
}

#jumbotron {
  background-image: url(/personal-profile/static/media/Elaina_Wallpaper_LI.765994b2.jpg);
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
}

#truepage {
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#photo {
  display: flex;
  justify-content: center;
}

#photo img {
  border-radius: 50%;
  width: 90%;
  min-width: 250px;
}

@media screen and (max-width: 576px) {
  #photo img {
    height: 300px;
    width: 300px;
  }
}
#identity {
  display: flex;
  justify-content: center;
  align-items: center;
}

#sometext {
  font-family: "Kaushan Script", cursive;
  margin-top: 4px;
  width: 95%;
  text-align: center;
}

#quote {
  font-family: "Roboto", cursive;
  font-size: 1rem;
}

#sosmed .bi {
  font-size: 30px;
  color: white;
}

#sosmed a .bi:hover {
  color: grey;
}

#skill {
  height: 100vh;
  background-image: url(/personal-profile/static/media/NepBlack.85aac3cd.jpg);
}

#skill02 {
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}

#skill02 h1 {
  font-family: "Kaushan Script", cursive;
  font-size: 1.5rem;
  display: flex;
  justify-content: center;
}

#frontend h2 {
  display: flex;
  justify-content: center;
  font-family: "Kaushan Script", cursive;
  font-size: 2rem;
}

#cpulogo {
  display: flex;
  justify-content: center;
  font-size: 200px;
}

@media screen and (max-width: 768px) {
  #cpulogo {
    display: none;
  }
}

#frontend #x {
  display: flex;
  justify-content: center;
}

#experience {
  background-image: url(/personal-profile/static/media/Elaina_Wallpaper_LI.765994b2.jpg);
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
}

#page {
  background-color: rgba(0, 0, 0, 0.5);
  color: black;
  height: 100vh;
}

#pagetext {
  font-family: "Kaushan Script", cursive;
  display: grid;
  justify-content: center;
}

#pageimg {
  display: grid;
  grid-template-columns: repeat(auto-fill, 375px);
  grid-row-gap: 20px;
  row-gap: 20px;
  justify-content: center;
}

#pageimg img {
  height: 150px;
  min-width: 200px;
  justify-self: center;
}

@media screen and (max-width: 768px) {
  .img4,
  .img5,
  .img6,
  .img7,
  .img8,
  .img9 {
    display: none;
  }
}

@media screen and (max-width: 1200px) {
  .img7,
  .img8,
  .img9 {
    display: none;
  }
}

